/* global jQuery  */
(function ($) {
	"use strict";

	$(document).ready(function () {

		$(".q-cpd-link").each(function(){

			var link = $(this);

			link.hover(
				function(){
					var linkData = link.data("target"),
						target = $("#" + linkData);

					target.addClass("hover");
				},

				function(){
					var linkData = link.data("target"),
						target = $("#" + linkData);
					target.removeClass("hover");
				}
			);
		});

		$(".q-cpd-box").each(function(){

			var link = $(this);

			link.hover(
				function(){
					var linkData = link.data("target"),
						target = $("#" + linkData);

					target.addClass("hover");
				},

				function(){
					var linkData = link.data("target"),
						target = $("#" + linkData);
					target.removeClass("hover");
				}
			);
		});
	});
})(jQuery);
